import React, { useMemo, useState, useRef, useEffect, useCallback } from 'react';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';
import { TrendingUp, X, Utensils, Car, Wrench, Gamepad2, MoreHorizontal } from 'lucide-react';
import { TransactionWithBalance } from '../types/transaction';

interface ExpenseChartProps {
  transactions: TransactionWithBalance[];
  loading: boolean;
}

interface DailyData {
  date: string;
  fullDate: string;
  dateKey: string;
  expense: number;
}

const formatCurrency = (amount: number): string => {
  if (amount >= 1000000) {
    return `${(amount / 1000000).toFixed(1)}jt`;
  }
  if (amount >= 1000) {
    return `${(amount / 1000).toFixed(0)}rb`;
  }
  return amount.toString();
};

const formatFullCurrency = (amount: number): string => {
  return new Intl.NumberFormat('id-ID', {
    style: 'currency',
    currency: 'IDR',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(amount);
};

const CustomTooltip = ({ active, payload, label }: any) => {
  if (active && payload && payload.length) {
    return (
      <div className="bg-[#1e1e2a] backdrop-blur-lg rounded-xl p-3 shadow-lg border border-white/10">
        <p className="text-sm font-semibold text-white mb-2">{payload[0]?.payload?.fullDate}</p>
        <div className="space-y-1">
          {payload.map((entry: any, index: number) => (
            <div key={index} className="flex items-center gap-2">
              <div 
                className="w-3 h-3 rounded-full" 
                style={{ backgroundColor: entry.color }}
              />
              <span className="text-sm text-white/70">{entry.name}:</span>
              <span className="text-sm font-bold" style={{ color: entry.color }}>
                {formatFullCurrency(entry.value)}
              </span>
            </div>
          ))}
          <p className="text-[10px] text-gray-500 mt-1 pt-1 border-t border-white/5">Klik untuk lihat detail</p>
        </div>
      </div>
    );
  }
  return null;
};

const getCatIcon = (cat: string | null) => {
  switch (cat) {
    case 'Foods': return <Utensils size={14} />;
    case 'Transportation': return <Car size={14} />;
    case 'Equipment': return <Wrench size={14} />;
    case 'Entertainment': return <Gamepad2 size={14} />;
    default: return <MoreHorizontal size={14} />;
  }
};

const ExpenseChart: React.FC<ExpenseChartProps> = ({ transactions, loading }) => {
  const [selectedDate, setSelectedDate] = useState<string | null>(null);
  const popupRef = useRef<HTMLDivElement>(null);

  const chartData = useMemo(() => {
    const dailyMap = new Map<string, { expense: number }>();
    
    const today = new Date();
    for (let i = 13; i >= 0; i--) {
      const date = new Date(today);
      date.setDate(date.getDate() - i);
      const dateKey = date.toISOString().split('T')[0];
      dailyMap.set(dateKey, { expense: 0 });
    }
    
    transactions.forEach((t) => {
      const dateKey = new Date(t.created_at).toISOString().split('T')[0];
      if (dailyMap.has(dateKey)) {
        const current = dailyMap.get(dateKey)!;
        if (t.type === 'expense') {
          current.expense += t.total;
        }
      }
    });
    
    const data: DailyData[] = [];
    dailyMap.forEach((value, key) => {
      const date = new Date(key);
      data.push({
        date: new Intl.DateTimeFormat('id-ID', { day: 'numeric', month: 'short' }).format(date),
        fullDate: new Intl.DateTimeFormat('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' }).format(date),
        dateKey: key,
        expense: value.expense,
      });
    });
    
    return data;
  }, [transactions]);

  const totalExpenseToday = useMemo(() => {
    const today = new Date().toISOString().split('T')[0];
    return transactions
      .filter(t => t.type === 'expense' && t.created_at.startsWith(today))
      .reduce((sum, t) => sum + t.total, 0);
  }, [transactions]);

  const selectedDateTransactions = useMemo(() => {
    if (!selectedDate) return [];
    return transactions.filter(
      t => t.type === 'expense' && t.created_at.startsWith(selectedDate)
    );
  }, [selectedDate, transactions]);

  const selectedDateLabel = useMemo(() => {
    if (!selectedDate) return '';
    const d = new Date(selectedDate);
    return new Intl.DateTimeFormat('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' }).format(d);
  }, [selectedDate]);

  const handleChartClick = useCallback((data: any) => {
    if (data && data.activePayload && data.activePayload.length > 0) {
      const dateKey = data.activePayload[0]?.payload?.dateKey;
      if (dateKey) {
        setSelectedDate(prev => prev === dateKey ? null : dateKey);
      }
    }
  }, []);

  // Close popup on outside click
  useEffect(() => {
    const handleClickOutside = (e: MouseEvent) => {
      if (popupRef.current && !popupRef.current.contains(e.target as Node)) {
        setSelectedDate(null);
      }
    };
    if (selectedDate) {
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [selectedDate]);

  if (loading) {
    return (
      <div className="bg-[#1a1a24] rounded-2xl p-5 border border-white/5 shadow-[0_0_30px_rgba(139,92,246,0.05)] mb-6">
        <div className="flex items-center gap-2 mb-4">
          <div className="w-8 h-8 bg-violet-500/15 rounded-lg flex items-center justify-center">
            <TrendingUp size={16} className="text-violet-400" />
          </div>
          <h2 className="text-base font-bold text-white">Grafik 14 Hari Terakhir</h2>
        </div>
        <div className="h-40 bg-white/5 rounded-xl animate-pulse" />
      </div>
    );
  }

  return (
    <div className="bg-[#1a1a24] rounded-2xl p-5 border border-white/5 shadow-[0_0_30px_rgba(139,92,246,0.05)] mb-6">
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <div className="w-8 h-8 bg-violet-500/15 rounded-lg flex items-center justify-center">
            <TrendingUp size={16} className="text-violet-400" />
          </div>
          <h2 className="text-base font-bold text-white">Grafik Pengeluaran 14 Hari</h2>
        </div>
        <div className="text-right">
          <p className="text-[10px] text-gray-500">Hari ini</p>
          <p className="text-xs font-bold text-rose-300">-{formatFullCurrency(totalExpenseToday)}</p>
        </div>
      </div>

      <div className="h-40">
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart data={chartData} margin={{ top: 10, right: 10, left: -10, bottom: 0 }} onClick={handleChartClick} style={{ cursor: 'pointer' }}>
            <defs>
              <linearGradient id="expenseGradient" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#f43f5e" stopOpacity={0.3} />
                <stop offset="95%" stopColor="#f43f5e" stopOpacity={0} />
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,0.04)" vertical={false} />
            <XAxis 
              dataKey="date" 
              axisLine={false}
              tickLine={false}
              tick={{ fontSize: 10, fill: 'rgba(255,255,255,0.35)' }}
              dy={5}
            />
            <YAxis 
              axisLine={false}
              tickLine={false}
              tick={{ fontSize: 10, fill: 'rgba(255,255,255,0.35)' }}
              tickFormatter={formatCurrency}
              dx={-5}
            />
            <Tooltip content={<CustomTooltip />} />
            <Area
              type="monotone"
              dataKey="expense"
              name="Pengeluaran"
              stroke="#fb7185"
              strokeWidth={2}
              fill="url(#expenseGradient)"
              dot={{ fill: '#fb7185', strokeWidth: 0, r: 3 }}
              activeDot={{ r: 5, stroke: 'rgba(255,255,255,0.5)', strokeWidth: 2 }}
            />
          </AreaChart>
        </ResponsiveContainer>
      </div>

      {/* Detail Popup */}
      {selectedDate && (
        <div ref={popupRef} className="mt-4 bg-[#12121a] rounded-xl border border-white/10 overflow-hidden animate-in">
          <div className="px-4 py-3 border-b border-white/5 flex items-center justify-between">
            <div>
              <p className="text-sm font-bold text-white">{selectedDateLabel}</p>
              <p className="text-[10px] text-gray-500 mt-0.5">
                {selectedDateTransactions.length} transaksi  Total {formatFullCurrency(selectedDateTransactions.reduce((s, t) => s + t.total, 0))}
              </p>
            </div>
            <button 
              onClick={() => setSelectedDate(null)}
              className="p-1.5 text-gray-500 hover:text-gray-300 hover:bg-white/5 rounded-lg transition-all"
            >
              <X size={14} />
            </button>
          </div>
          {selectedDateTransactions.length === 0 ? (
            <div className="px-4 py-6 text-center">
              <p className="text-gray-500 text-sm">Tidak ada pengeluaran</p>
            </div>
          ) : (
            <div className="divide-y divide-white/5 max-h-[200px] overflow-y-auto">
              {selectedDateTransactions.map((t) => (
                <div key={t.id} className="px-4 py-2.5 flex items-center gap-3">
                  <div className="w-8 h-8 rounded-lg bg-rose-500/15 text-rose-400 flex items-center justify-center flex-shrink-0">
                    {getCatIcon(t.expense_category)}
                  </div>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-1.5">
                      <span className="text-xs font-semibold text-gray-200">{t.expense_category || 'Expense'}</span>
                      {t.notes && <span className="text-xs text-gray-500 truncate"> {t.notes}</span>}
                    </div>
                    <p className="text-[10px] text-gray-600 mt-0.5">
                      {formatFullCurrency(t.price)}  {t.quantity}
                    </p>
                  </div>
                  <span className="text-xs font-bold text-rose-400 whitespace-nowrap">
                    -{formatFullCurrency(t.total)}
                  </span>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Quick Stats */}
      <div className="mt-4 pt-4 border-t border-white/5">
        <div className="bg-rose-500/10 rounded-xl p-3">
          <p className="text-[10px] text-rose-400 font-medium mb-1">Total Pengeluaran (14 hari)</p>
          <p className="text-base font-bold text-rose-400">
            {formatFullCurrency(chartData.reduce((sum, d) => sum + d.expense, 0))}
          </p>
        </div>
      </div>
    </div>
  );
};

export default ExpenseChart;
